import service from '../../service.js';
import {
	mapState,
	mapMutations
} from 'vuex'
import mInput from '../../components/m-input.vue'

export default {
	components: {
		mInput
	},
	data() {
		return {
			loginType: 'phone',
			pageType: "login",
			state: false, //是否开启倒计时
			totalTime: 60, //总时间，单位秒
			recordingTime: 0, //记录时间变量
			currentTime: 0, //显示时间变量
			vaild: "",
			mobile: '',
			password: '',
			account: '',
			positionTop: 0
		}
	},
	onLoad() {
		let that = this;
		uni.login({
			provider: 'weixin',
			success: function(loginRes) {
				that.getOpenId(loginRes.code);
			}
		});
	},
	computed: mapState(['forcedLogin']),
	methods: {
		...mapMutations(['login', 'setOpenId']),
		inputChange(e) {
			const key = e.currentTarget.dataset.key;
			this[key] = e.detail.value;
		},
		async loginWithWe(code) {
			var res = await this.$req.ajax({
				path: '/wxapi/login/WeChat',
				title: '正在加载',
				data: {
					WeChatID: code,
					verify: "zhongbao"

				}
			});
			if (res.data.code == 200) {
				console.log(res.data.data)
				uni.redirectTo({
					url: "/pages/main/main"
				})
			} else {
				this.$api.msg(res.data.message);
			}
		},
		async getOpenId(code) {
			var res = await this.$req.ajax({
				path: '/wxapi/login/getOpenid',
				title: '正在加载',
				data: {
					code: code,
					appid: "wx7aa0d26e5ca6597c",
					secret: "a43098ef40806ae89c1711ab3b9a6e15"
				}
			});
			if (res.data.code == 200) {
				let resdata = JSON.parse(res.data.data.code);
				this.loginWithWe(resdata.openid)
				this.setOpenId(resdata.openid)

			}
		},
		async toRegister() {
			if (!/(^1[3|4|5|6|7|8|9][0-9]{9}$)/.test(this.mobile)) {
				this.$api.msg('请输入正确的手机号码');
				return;
			}
			let res = await this.$req.ajax({
				path: '/wxapi/Login/registered',
				title: '正在加载',
				data: {
					account: this.mobile,
					LoginPWD: this.password,
					code: this.vaild,

				}
			});
			if (res.data.code == 200) {
				this.login(res.data.data.token);
				uni.redirectTo({
					url: "/pages/main/main"
				})
			} else {
				this.$api.msg(res.data.message)

			}
		},
		async checking() {
			if (!/(^1[3|4|5|6|7|8|9][0-9]{9}$)/.test(this.mobile)) {
				this.$api.msg('请输入正确的手机号码');
				return;
			}
			// 
			let res = await this.$req.ajax({
				path: 'wxapi/sms/ChuanglanSmsApi',
				title: '正在加载',
				data: {
					account: this.mobile,

				}
			});
			if (res.data.code == 200) {
				//把显示时间设为总时间
				this.currentTime = this.totalTime;
				//开始倒计时
				this.state = true;
				//执行倒计时
				this.checkingTime();
			} else {
				this.$api.msg(res.data.message)

			}

		},
		checkingTime() {
			let that = this;
			//判断是否开启
			if (this.state) {
				//判断显示时间是否已到0，判断记录时间是否已到总时间
				if (this.currentTime > 0 && this.recordingTime <= this.totalTime) {
					//记录时间增加 1
					this.recordingTime = this.recordingTime + 1;
					//显示时间，用总时间 - 记录时间
					this.currentTime = this.totalTime - this.recordingTime;
					//1秒钟后，再次执行本方法
					setTimeout(() => {
						//定时器内，this指向外部，找不到vue的方法，所以，需要用that变量。
						that.checkingTime();
					}, 1000)
				} else {
					//时间已完成，还原相关变量
					this.state = false; //关闭倒计时
					this.recordingTime = 0; //记录时间为0
					this.currentTime = this.totalTime; //显示时间为总时间
				}
			} else {
				//倒计时未开启，初始化默认变量
				this.state = false;
				this.recordingTime = 0;
				this.currentTime = this.totalTime;
			}
		},
		async toLoginPw(){
			 
			let res = await this.$req.ajax({
				path: '/wxapi/login/Loginpwd',
				title: '正在加载',
				data: {
					account: this.account,
					LoginPWD: this.password,
			
				}
			});
			if (res.data.code == 200) {
				this.login(res.data.data.token);
				uni.redirectTo({
					url: "/pages/main/main"
				})
			} else {
				this.$api.msg(res.data.message)
			
			}
		},
		async toLogin() {
			this.logining = true;
			const {
				mobile,
				vaild
			} = this;
			if (!/(^1[3|4|5|6|7|8|9][0-9]{9}$)/.test(mobile)) {
				this.$api.msg('请输入正确的手机号码');
				return;
			}
			const sendData = {
				mobile,
				vaild
			};

			const result = await this.$req.ajax({
				path: 'wxapi/login/makerLogin',
				title: '正在加载',
				data: sendData
			});
			if (result.data.code === 200) {
				this.login(result.data.data.token);
				uni.navigateTo({
					url: "/pages/main/main"
				});
			} else {
				this.$api.msg(result.data.message);
				this.logining = false;
			}
		}
	}
}
